<template>
    <div class="container">
        <div class="row justify-content-center">
            <div class="col-lg-8 card">
                <div class="content is-medium p-3">
                    <h3>{{ post.title }}</h3>
                    <p>
                        {{ post.content }}
                    </p>
                </div>
                <div class="card-footer">
                    <facebook :url="url" scale="2" class="m-2 btn-share"></facebook>
                    <twitter :url="url" scale="2" class="m-2 btn-share"></twitter>
                    <linkedin :url="url" scale="2" class="m-2 btn-share"></linkedin>
                    <google :url="url" scale="2" class="m-2 btn-share"></google>
                    <email :url="url" class="m-2 btn-share" subject="Hello" scale="2"></email>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        Facebook,
        Twitter,
        Linkedin,
        Email,
        Google
    } from "vue-socialmedia-share";
    export default {
        props: ['id'],
        data() {
            return {
                //url: window.location.href
                url: 'http://mahmoudrady.com',
                liked: localStorage.getItem('likepost'),
            }
        },
        computed: {
            post() {
                return this.$store.getters.loadedPost(this.id)
            }
        },
        components: {
            Facebook,
            Twitter,
            Linkedin,
            Email,
            Google
        },


    }
</script>


<style>
    .btn-share {
        cursor: pointer;
        display: inline-block;
    }
</style>